  font-style: normal;
}
html,
body {
  height: 100%;
}

body {
  margin: 0;
  min-width: 320px;
}
body::-webkit-scrollbar {
  display: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  vertical-align: middle;
}

a {
  text-decoration: none;
}

.feedback {
  color: #000000;
}

.feedback ul {
  list-style: none;
}

.feedback p {
  margin-bottom: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  overflow: hidden;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.button {
  display: inline-block;
  margin: 0;
  padding: 10px 20px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 20px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  background-color: #68b738;
  border-radius: 0;
  border: none;
  cursor: pointer;
  word-wrap: break-word;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:focus-visible {
  outline: none;
  background-color: #5eaa2f;
}
@supports not selector(:focus-visible) {
  .button:focus {
    outline: none;
    background-color: #5eaa2f;
  }
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 26px;
    padding: 14px 26px;
  }
}

.button--light {
  background-color: #f2f2f2;
  color: #444444;
}
.button--light:hover {
  background-color: #ebebeb;
}
.button--light:focus-visible {
  outline: none;
  background-color: #ebebeb;
}
@supports not selector(:focus-visible) {
  .button--light:focus {
    outline: none;
    background-color: #ebebeb;
  }
}
.button--light:active {
  color: inherit;
}

.field {
  width: 100%;
  height: 45px;
  padding: 13px 15px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  background-color: #ffffff;
  border: 1px solid #e7e7e7;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .field {
    padding: 11px 20px;
    font-size: 20px;
    line-height: 20px;
    height: 52px;
  }
}

.field:hover {
  border: 1px solid #cdcdcd;
}

.field:user-invalid:required {
  border: 2px solid #ff8282;
}

.field:user-invalid:required + .owner-contacts__icon {
  stroke: #ff8282;
}

.field:focus-visible {
  outline: none;
  border: 2px solid #444444;
}

.field:focus-visible + .owner-contacts__icon {
  stroke: #68b738;
}

@supports not selector(:focus-visible) {
  .field:focus {
    outline: none;
    border: 2px solid #444444;
  }
}
@supports not selector(:focus-visible) {
  .field:focus + .owner-contacts__icon {
    stroke: #68b738;
  }
}
.field:focus-visible:not(:placeholder-shown) {
  border: 2px solid #444444;
}

.field:autofill,
.field:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 100px #ffffff;
  box-shadow: inset 0 0 0 100px #ffffff;
}

.field:disabled,
.field-disabled {
  opacity: 0.3;
  pointer-events: none;
}

.field[type=number]::-webkit-outer-spin-button,
.field[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.field[type=number] {
  -moz-appearance: textfield;
  -webkit-appearance: textfield;
  appearance: textfield;
}

