.icon-menu {
  position: relative;
  width: 24px;
  height: 24px;
  cursor: pointer;
  z-index: 5;
  border: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .icon-menu {
    display: none;
  }
}
.icon-menu span, .icon-menu::before, .icon-menu::after {
  content: "";
  transition: all 0.3s ease 0s;
  right: 0;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #000000;
}
.icon-menu::before {
  top: 2px;
}
.icon-menu::after {
  bottom: 4px;
}
.icon-menu span {
  top: calc(50% - 2px);
}

.menu {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .menu {
    padding-right: 4px;
    height: 100%;
  }
}
@media (min-width: 1440px) {
